* {
  box-sizing: border-box;
}

html {
  &, body {
    &, #root {
      width: 100%;
      height: 100%;
      margin: 0;
      padding: 0;
      background-color: #272727;
      -webkit-touch-callout: none;
      -webkit-user-select: none;
      -khtml-user-select: none;
      -moz-user-select: none;
      -ms-user-select: none;
      user-select: none;
      overflow: hidden;
    }
  }

  body {
    #root {
      .main {
        position: relative;
        width: 100%;
        height: 100%;
        color: white;
        overflow: hidden;


        canvas {
          width: 100%;
          height: 100%;
          position: absolute;
          top: 0;
          // pointer-events: none;
          overflow: hidden;
        }
      }
    }


    position: fixed;
    overflow: hidden;
    overscroll-behavior-y: none;
    font-family: -apple-system, BlinkMacSystemFont, avenir next, avenir, helvetica neue, helvetica, ubuntu, roboto, noto, segoe ui, arial,
      sans-serif;
    color: black;
  }
}
      




